<script>
import {
  getProjectDataAll,
  searchProjectData,
  getTimeLineData,
} from "api/projects";
import PageHeader from "components/common/menu/PageHeader";
import { Message } from "element-ui";

export default {
  name: "ProjectList",
  components: {
    PageHeader,
  },
  data() {
    return {
      activities: [],
      project_name: "",
      project_code: "",
      customer_name: "",
      personnel_name: "",
      synopsis: "",
      dialogTimeLineVisible: false,
      table_data: [],
      loading: false,
      search: "",
    };
  },
  created: function () {
    this.getProjectDataAll();
  },
  methods: {
    getProjectDataAll() {
      this.loading = true;
      getProjectDataAll()
        .then((res) => {
          if (res.data.status === 1) {
            this.table_data = res.data.data;
            this.loading = false;
          } else {
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    searchProjectData() {
      let values = {};
      values["project_code"] = this.ruleForm.project_code;
      values["project_name"] = this.ruleForm.project_name;
      values["customer_name"] = this.ruleForm.customer_name;
      values["is_delete"] = this.ruleForm.is_delete;
      searchProjectData(values)
        .then((res) => {
          if (res.data.status === 1) {
            this.tableData = res.data.data;
            this.loading = false;
          } else {
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getTimeLineData(project_code) {
      let values = {};
      values["project_code"] = project_code;
      getTimeLineData(values)
        .then((res) => {
          if (res.data.status === 1) {
            this.activities = res.data.data;
            this.dialogTimeLineVisible = true;
            console.log(this.activities);
          } else {
            Message({
              showClose: true,
              message: res.data.message,
              type: "error",
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchProjectData();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    createBtn() {
      this.$router.push({ path: "Projectform" });
    },
    handleEdit(index, row) {
      console.log(row);
      this.$router.push({
        path: "Projectform",
        query: { project_code: row.project_code },
      });
    },
    handleDelete(index, row) {
      console.log(row.project_code);
      this.$router.push({
        path: "Projectform",
        query: { project_code: row.project_code },
      });
    },
    timeLine(index, row) {
      this.getTimeLineData(row.project_code);
    },
    getPdffile() {
      console.log("PDF");
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    title(activity) {
      let status = activity.status;
      let changed = activity.changed;
      let type = activity.type;
      switch (status) {
        case "PROJECT":
          status = "案件";
          break;
        case "ESTIMATE":
          status = "見積書";
          break;
        case "ORDER":
          status = "注文書";
          break;
        case "DELIVERY":
          status = "納品書";
          break;
        case "ACCEPTANCE":
          status = "検収書";
          break;
        case "INVOICE":
          status = "請求書";
          break;
        default:
          break;
      }
      switch (type) {
        case "INSERT":
          type = "作成";
          break;
        case "UPDATE":
          type = "更新";
          break;
        default:
          break;
      }
      return status + changed + type;
    },
    body(activity) {
      let change_at = activity.change_at;
      let change_by = activity.change_by;
      let type = activity.type;
      let datestr = change_at.toString();
      switch (activity.type) {
        case "INSERT":
          type = "作成";
          break;
        case "UPDATE":
          type = "更新";
          break;
        default:
          break;
      }
      return (
        change_by +
        "が" +
        datestr.slice(0, 4) +
        "/" +
        datestr.slice(5, 7) +
        "/" +
        datestr.slice(8, 10) +
        " " +
        datestr.slice(11, 13) +
        ":" +
        datestr.slice(14, 16) +
        ":" +
        datestr.slice(17, 19) +
        "に" +
        type
      );
    },
    icon(activity) {
      let icon;
      switch (activity.type) {
        case "INSERT":
          icon = "el-icon-circle-plus-outline";
          break;
        case "UPDATE":
          icon = "el-icon-circle-check";
          break;
        default:
          break;
      }
      return icon;
    },
    color(activity) {
      let color;
      switch (activity.status) {
        case "PROJECT":
          color = "#3F6F8D";
          break;
        case "ESTIMATE":
          color = "#4D2D22";
          break;
        case "ORDER":
          color = "#FE8534";
          break;
        case "DELIVERY":
          color = "#FCCA46";
          break;
        case "ACCEPTANCE":
          color = "#A1C181";
          break;
        case "INVOICE":
          color = "#619B8A";
          break;
        default:
          break;
      }
      return color;
    },
  },
};
</script>

